.App {
  text-align: center;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.timer {
  min-height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.timer-box {
  display: flex;
  width: 10vw;
  height: 10vw;
  background-color: #282c34;
  color: white;
  justify-content: center;
  align-items: center;
  border: 1px solid white;
  border-radius: 5px;
  font-size: 2vw;
  font-weight: bolder;
}

.button {
  width: 10vw;
  height: 6vw;
  background-color: #282c34;
  color: white;
  font-size: 1vw;
  font-weight: bold;
  border-radius: 5px;
}

button:active {
  border: none;
}
